.block-newsletter {
  @include section-wrap(100%);
  .wrapper {
    background-color: $bg-green;
    border-radius: 8px;

    &__bg-wrap,
    .bg-wrap {
      padding: 32px 0 45px 0;

      &__head {
        @include font(24px, 700, 29px, $text-light, center);
      }
      &__desc {
        @include font(16px, 400, 22px, $text-light, center);
        margin-top: 8px;
        margin-bottom: 24px;
      }
      &__subscribe-wrap,
      .subscribe-wrap {
        text-align: center;
        display: flex;
        justify-content: center;

        &__input {
          background: $bg-stats;
          border: $border-light;
          border-radius: 4px 0px 0px 4px;
          height: 40px;
          width: 280px;
          text-indent: 5px;
        }
        &__input:valid .accent-btn {
          background-color: rgb(208, 127, 255);
        }

        &__accent-btn,
        .accent-btn {
          @include button(27px, 23px, $bg-green);
          border-radius: 0px 4px 4px 0px;
          // color: $text-subscribe-btn;
          @include font(16px, 700, 19.2px, $text-subscribe-btn, center);
          background: rgba(255, 255, 255, 0.85098);
          height: 42px;

          &:hover {
            @include button-hover-active($text-link);
            transform: scale(1);
          }
          &:active {
            @include button-hover-active($btn-active);
            transform: scale(1);
          }
        }
      }
    }
  }
}

// Tablet: 1024px-1440px
@media (min-width: $table-width) and (max-width: $table-big-width) {
  .block-newsletter {
    @include section-wrap(1199px);
  }
}

// Tablet: 768px-1023px
@media (max-width: $table-min-width) {
  .block-newsletter {
    @include section-wrap(100%);
    // width: 100%;
  }
}
